.informacion{
  background: rgba( 162, 68, 255, 0.20 );
  box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
  backdrop-filter: blur( 5.5px );
  -webkit-backdrop-filter: blur( 5.5px );
  border-radius: 10px;
  border: 1px solid rgba( 255, 255, 255, 0.18 );
  border: 1px solid rgba( 255, 255, 255, 0.18 );
display: -webkit-flex;
display: flex;
-webkit-justify-content: center;
        justify-content: center;
-webkit-align-items: center;
        align-items: center;
height: 400px;
transition: all 1s;
width: 900px;
-webkit-flex-direction: column;
        flex-direction: column;

border-radius:16px ;
}


.informacion p {
  font-family: 'Montserrat', sans-serif;
  font-size: 35px;
font-weight: 800;

font-style: italic;
  color:aliceblue;
  display: inline-block;
  padding: 15px;
  padding-left: 20px;
  max-width: 70%;
  text-align: center;
}

.informacion h1{
  font-family: 'Montserrat', sans-serif;
  font-size: 20px;
  font-weight: 500;
   text-align: left;

}

.boton{

display: -webkit-flex;

display: flex;
-webkit-justify-content: center;
        justify-content: center;
-webkit-align-items: center;
        align-items: center;

}
.boton span a i{
   color: rgba(255, 255, 255, 0.787);
   margin: 0px;
   padding: 0px;
   transition: all .6s ease-out;
font-size: 50px;

}
.boton span a i:hover{
  color: #E100FF;
  font-size: 100px;
  transition: all .3s ease;
}


/* media query del texto a 1920px*/
@media (max-width :1000px) {
  .informacion{
    height: 550px;
  }
  .informacion h1{
    letter-spacing: 4px;
  }
}
@media (max-width :900px){
  .informacion{
    transition: all 1s;
    width: 650px;
    padding-top: 50px;
  }
  .informacion h1{
    font-size: 20px;
    font-weight: 600;
 
    transition: all 1s;
  }
  .informacion p{
    font-size: 30px;
  }

  .boton span a i{
  
  
 font-size: 40px;
 
 }

} 

@media (max-width :600px){
  .informacion{
    margin-top: 100px;
    transition: all 1s;
    width: 400px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding-top: 0px;
    padding: 10px;
  }
  .informacion h1{
    font-size: 14px;
font-weight: 300;
text-align: center;
  }
  .informacion p{
    font-size: 18px;
    font-weight: 800;
  }
  .boton span a i{
    color: aliceblue;
  
 font-size: 35px;
 
 }

} 

@media (max-width :400px){
  .informacion{

    width: 280px;
    height: 500px;
    margin-top: 50px;
  
  }
  .informacion h1{
    font-size: 12px;
    
    transition: all 1s;
  }
  .informacion p{
    font-size: 14px;
    letter-spacing: 2px;
    padding-top: 20px;
    padding-left: 5px;
    padding-right: 5px;
  }
  .boton span a i{
visibility: hidden;
 
 }

} 
